				<el-form-item :style='{"border":"0px solid #eee","padding":"0","margin":"0 0 16px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else class="input" label="年级" prop="nianji">
					<el-input v-model="ruleForm.nianji"
						placeholder="年级" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0","margin":"0 0 16px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="select" v-if="type!='info'"  label="班级" prop="banji">
					<el-select :disabled="ro.banji" v-model="ruleForm.banji" placeholder="请选择班级" >
						<el-option
							v-for="(item,index) in banjiOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0","margin":"0 0 16px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else class="input" label="班级" prop="banji">
					<el-input v-model="ruleForm.banji"
						placeholder="班级" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0","margin":"0 0 16px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="input" v-if="type!='info'"  label="学生手机" prop="xueshengshouji">
					<el-input v-model="ruleForm.xueshengshouji" placeholder="学生手机" clearable  :readonly="ro.xueshengshouji"></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0","margin":"0 0 16px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else class="input" label="学生手机" prop="xueshengshouji">
					<el-input v-model="ruleForm.xueshengshouji" placeholder="学生手机" readonly></el-input>
